import React, { useState, useEffect } from 'react';
import axios from 'axios';


import Battery50Icon from '@mui/icons-material/Battery50';
import EmojiEmotionsIcon from '@mui/icons-material/EmojiEmotions';
import LocalFireDepartmentIcon from '@mui/icons-material/LocalFireDepartment';
import DirectionsRunIcon from '@mui/icons-material/DirectionsRun';
import LightIcon from '@mui/icons-material/Light';
import FaceIcon from '@mui/icons-material/Face';

import Typography from '@mui/material/Typography';

function Statusitem(props) {
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [data, setData] = useState(null);

    const GetStatus = async () => {
        setLoading(true);
       // console.log('http://localhost:3001/getstatus/'+props.deviceId);
        try {
          const response = await axios.get('https://leantechsmarthome-0db22cab28f1.herokuapp.com/getstatus/'+props.deviceId);
            setData(response.data);
        } catch (error) {
          console.error('Error making POST request:', error.message);
          return 'Error: ' + error.message;
        }  finally {
          // Set loading to false whether the request was successful or not 
    setLoading(false); 
        }
      }
      useEffect(() => {
        GetStatus();
      }, ([]));   
  return (
    data!==null?  
    props.devicename === ''?
    <Typography variant="body2"><LocalFireDepartmentIcon/> {props.devicename} : {data[0].value==="normal"? <><EmojiEmotionsIcon sx={{color:'greenYellow'}}/> </>  :''}  : <Battery50Icon sx={{color:'greenYellow'}}/> {data[1].value} %</Typography>
    : 
    <Typography variant="body2"><FaceIcon/> {props.devicename} : {data[0].value==="presence"? <><DirectionsRunIcon sx={{color:'greenYellow'}}/> </>  :''}  : <LightIcon sx={{color:'greenYellow'}}/> {data[4].value} lux</Typography>

    :
    <div></div> 
  )
}

export default Statusitem